@use "vars" as *;

/* ====================================================
HTML
==================================================== */
html {
	font-size: 62.5%;
}

body {
	color: #464646;
	@include font-fm($font-base, 400); 
	font-size: 100%;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	line-height: 1;
	background: #fff;
	overflow-x: hidden;
}

a {
	display: inline-block;
	color: inherit;
	text-decoration: none;
	outline: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}
