.js-u__scrollin {
  opacity: 0;
  -webkit-transform: translate(0, 70px);
  -ms-transform: translate(0, 70px);
  transform: translate(0, 70px);
}

.js-u__scrollin.is-visible {
  opacity: 1;
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  transform: translate(0, 0);
  -webkit-transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  -o-transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.js-animation-image {
  background: #464646;
}
.js-animation-image img {
  transition: 0.5s transform cubic-bezier(0.19, 1, 0.22, 1);
  opacity: 0;
}
.js-animation-image.is-visible img {
  animation: image_reveal 1s cubic-bezier(0.19, 1, 0.22, 1) both;
  opacity: 1;
}
.js-animation-image.showed img {
  animation: none;
}

.js-scrollin.is-visible span::after {
  right: 0;
  transition: right 0.8s cubic-bezier(0.55, 0.05, 0.22, 0.99);
  transition-delay: 0.5s;
}

@keyframes image_reveal {
  0% {
    clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
    transform: scale(1.3);
    opacity: 0;
  }
  100% {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    transform: scale(1);
    opacity: 1;
  }
}
